export function userGreetings(name: string): string {
  const localTime = new Date().getHours();
  let greeting;

  if (localTime >= 4 && localTime < 11) {
    greeting = ` , ${name}!`;
  } else if (localTime >= 11 && localTime < 17) {
    greeting = ` , ${name}!`;
  } else if (localTime >= 17 && localTime < 21) {
    greeting = ` , ${name}!`;
  } else {
    greeting = ` , ${name}?`;
  }

  return greeting;
}
